import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
import kmSelect from '../../components/km-select.vue'
import mpvuePicker from '../../components/mpvue-picker/mpvuePicker.vue';
import timeData from '../../common/time.data.js';
import util from '../../common/util.js'
export default {
	components: {
		uniCalendar,
		kmSelect,
		mpvuePicker
	},
	data() {
		/**
		 * 时间计算
		 */
		function getDate(date, AddDayCount = 0) {
			if (typeof date !== 'object') {
				date = date.replace(/-/g, '/');
			}
			let dd = new Date(date);
			dd.setMonth(dd.getMonth() + AddDayCount); // 获取AddDayCount天后的日期
			let y = dd.getFullYear();
			let m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1; // 获取当前月份的日期，不足10补0
			let d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate(); // 获取当前几号，不足10补0
			return y + '-' + m + '-' + d;
		}
		return {
			slide: 'vertical',
			date: '',
			startDate: '',
			endDate: '',
			timeData: {},
			start_time:'',
			pickerValueArray:[],
			mulLinkageTwoPicker: timeData,
			themeColor: '#007AFF',
			mode: '',
			deepLength: 1,
			pickerValueDefault: [0],
		};
	},
	onLoad() {},
	methods: {
		toggle(index, item) {
			this.tags[index].checked = !item.checked;
			// item.checked = !item.checked;
			if (index === 'horizontal') {
				this.tags['vertical'].checked = false;
			}
			if (index === 'vertical') {
				this.tags['horizontal'].checked = false;
			}
			// this.attribute[item.attr] = !item.checked;
		},
		change(e) {
			var _this = this;
			this.timeData = e;
			uni.request({
				url:_this.serverURL+'home/index/erp_time',
				method:"POST",
				data:{
					set_time:e.fulldate
				},
				success(res) {
					uni.setStorageSync("componentTime", res.data.time);
				}
			})
		},
		toClick(e) {
			var _this = this;
			this.timeData = e;
			uni.request({
				url:_this.serverURL+'home/index/erp_time',
				method:"POST",
				data:{
					set_time:e.fulldate
				},
				success(res) {
					uni.setStorageSync("componentTime", res.data.time);
				}
			})
			
		},
		showMulLinkageTwoPicker() {
			this.pickerValueArray = this.mulLinkageTwoPicker
			this.mode = 'multiLinkageSelector'
			this.deepLength = 2
			this.pickerValueDefault = [0, 0]
			this.$refs.mpvuePicker.show()
		},
		onConfirm(e) {
			this.start_time = (e.value[0]>=10?(e.value[0]):('0'+e.value[0])) + ':' + (e.value[1]>=10?(e.value[1]):('0'+e.value[1]))
		},
		onCancel(e) {
			console.log(e)
			this.start_time = (e.value[0]>=10?(e.value[0]):('0'+e.value[0])) + ':' + (e.value[1]>=10?(e.value[1]):('0'+e.value[1]))
		},
		selectedTime() {
			/* uni.setStorageSync("componentTimeInfo", this.start_time);
			uni.navigateBack(); */
			/* uni.showToast({
				title:'请选择'
			}) */
			var time = uni.getStorageSync("componentTime")+" "+this.start_time;
			console.log(time);
			var _this = this;
			uni.request({
				url:_this.serverURL+'home/index/erp_hour',
				method:"POST",
				data:{
					set_time:time
				},
				success(res) {
					if(res.data.time==false){
						uni.showToast({
							title:'具体装货时间有误',
							icon:'none'
						})
					} else {
						uni.setStorageSync("componentTimeInfo", _this.start_time);
						uni.navigateBack();
					}
				}
			})
		}
	}
};
